@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);

*{
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  color: white;
  background-color: #252326; ;
}

body{
  margin: 0;
}
#container-main{width:100%;height:100vh;display:flex;position:absolute;overflow:hidden}
#container-board{flex:1 1;height:100%;display:flex;justify-content:center;align-items:center}#board{border:1px solid rgba(200,200,200,.4);display:flex;flex-wrap:wrap}
.cell{height:40px;width:40px;border:1px solid rgba(200,200,200,.2);cursor:pointer;outline:none;display:flex;justify-content:center;align-items:center;color:#fff;font-size:16px;font-family:"Roboto",sans-serif}
.menu-container{height:100vh;border-left:1px solid rgba(200,200,200,.2);transition:width .5s}#section-menu-container{width:100%;height:auto;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;padding:20px 20px;border-bottom:1px dashed rgba(200,200,200,.4)}#button-reset{width:auto;height:auto;padding:8px 18px;background:transparent;border:1px solid rgba(200,200,200,.4);outline:none;display:flex;justify-content:center;align-items:center;border-radius:100px;cursor:pointer}.container-connect{width:auto;height:auto}.board-size-input{width:60px;height:auto;padding:5px 3px;border-radius:2px;border:1px solid rgba(200,200,200,.4);background-color:rgba(0,0,0,0);color:#fff;outline:none;display:block;margin-right:20px}.board-size-label{font-size:12px;letter-spacing:1px;display:block}.number-of-bombs-label{font-size:12px;letter-spacing:1px;display:block}.number-of-bombs-input{width:60px;height:auto;padding:5px 3px;border-radius:2px;border:1px solid rgba(200,200,200,.4);background-color:rgba(0,0,0,0);color:#fff;outline:none;display:block;margin-right:20px}.hamburger-button{width:40px;height:auto;background-color:rgba(0,0,0,0);outline:none;border:none;cursor:pointer;padding:0px}.hamburger-line{width:100%;height:4px;background-color:#fff;border-radius:100px;margin-bottom:5px;display:block}#button-submit-board-settings{min-width:80px;min-height:20px;width:auto;height:auto;padding:8px 18px;font-size:16px;background-color:#558b2f;display:flex;justify-content:flex-start;align-items:center;color:#fff;font-size:16px;letter-spacing:1px;border-radius:100px;outline:none;border:none;margin-left:auto;margin-top:30px;cursor:pointer}
#container-login{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;padding:10px}#login-form{width:600px;display:flex;flex-wrap:wrap}@media screen and (max-width: 600px){#login-form{width:100%;height:auto;padding:10px}}#login-fieldset{border:1px dotted rgba(200,200,200,.2);border-radius:10px}#login-legend{font-size:50px;letter-spacing:5px;font-family:"Indie Flower",cursive;color:#558b2f}.login-input{width:100%;height:auto;padding:8px 3px;border:1px solid rgba(200,200,200,.4);outline:none;letter-spacing:1px;margin-bottom:15px;border-radius:5px;font-size:20px;color:#c8c8c8}.login-label{color:rgba(200,200,200,.6);letter-spacing:1px;text-transform:uppercase;font-size:14px}#login-error{display:inline-block;width:100%;text-align:center;color:crimson;font-size:.8rem;letter-spacing:1px}.button-submit-login{width:auto;height:auto;padding:8px 18px;font-size:1rem;letter-spacing:1px;background-color:#558b2f;display:flex;justify-content:flex-start;align-items:center;color:#fff;border-radius:100px;outline:none;border:none;margin-left:auto;margin-top:10px;cursor:pointer}#register-link{margin-top:15px;margin-left:auto;text-decoration:none;letter-spacing:2px}
#container-register{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;padding:10px}#register-form{width:600px;display:flex;flex-wrap:wrap}@media screen and (max-width: 600px){#register-form{width:100%;height:auto;padding:10px}}#register-fieldset{border:1px dotted rgba(200,200,200,.2);border-radius:10px}#register-legend{font-size:50px;letter-spacing:5px;font-family:"Indie Flower",cursive;color:#558b2f}#register-error{display:inline-block;width:100%;text-align:center;color:crimson;font-size:.8rem;letter-spacing:1px}.register-input{width:100%;height:auto;padding:8px 3px;border:1px solid rgba(200,200,200,.4);outline:none;letter-spacing:1px;margin-bottom:15px;border-radius:5px;font-size:20px;color:#c8c8c8}.register-label{color:rgba(200,200,200,.6);letter-spacing:1px;text-transform:uppercase;font-size:14px}.button-submit-register{width:auto;height:auto;padding:8px 18px;font-size:1rem;letter-spacing:1px;background-color:#558b2f;display:flex;justify-content:flex-start;align-items:center;color:#fff;border-radius:100px;outline:none;border:none;margin-left:auto;margin-top:10px;cursor:pointer}#login-link{margin-top:15px;margin-left:auto;text-decoration:none;letter-spacing:2px}
